.App {
  max-width: 1200px;
  min-width: 800px;
  padding: 100px;
  margin: auto;
}

.Input-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.title {
  display: flex;
  justify-content: center;
}

.List-section {
  display: flex;
  gap: 12px;
}

.list-box {
  width: 100px;
  height: 100px;
  border: 1px solid purple;
  border-radius: 10px;
}
